.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode blockquote, .dark-mode td {
  color: #e1e1e1;
}
.dark-mode .precio {
  color: #71B100;
}
.dark-mode .anuncio {
  background-color: #333333;
}
.dark-mode .anuncio .icono {
  filter: invert(90%);
}
.dark-mode .resumen-propiedad .icono {
  filter: invert(90%);
}
.dark-mode .form label, .dark-mode .form legend, .dark-mode .form p {
  color: #e1e1e1;
}
.dark-mode .form input:not([type=submit]), .dark-mode .form textarea,
.dark-mode .form select {
  background-color: #333333;
  color: #FFFFFF;
  border: none;
  background-image: #FFFFFF;
}
.dark-mode .form input[type=date]::-webkit-calendar-picker-indicator,
.dark-mode .form input[type=time]::-webkit-calendar-picker-indicator {
  filter: invert(100%);
}
.dark-mode table.propiedad tbody tr {
  background-color: #1a1a1a;
}
.dark-mode table.propiedad tbody tr:nth-child(odd) {
  background-color: #1a1a1a;
}

.nombre-pagina {
  text-align: center;
  margin: 3rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    max-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: url(../img/baner.jpg);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: 100vh;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    height: 100vh;
    overflow: scroll;
    overflow-x: hidden;
  }
  .contenedor-app .app::-webkit-scrollbar {
    width: 0.9rem;
  }
  .contenedor-app .app::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 5px;
  }
}

@media (min-width: 768px) {
  .barra-admin {
    display: flex;
    justify-content: space-between;
  }
}

.seccion {
  display: none;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
}
.seccion .texto-centrado {
  text-align: center;
}

.mostrar {
  display: block;
}

.barra {
  background-color: rgba(8, 146, 226, 0.8431372549);
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  transition-property: background-color;
  transition-duration: 500ms;
  transition-timing-function: linear;
}
.barra .boton {
  background-color: #333333;
}
.barra .boton:hover {
  background-color: #4d4d4d;
}

.servicio .nombre-servicio {
  color: #000000;
}

.listado-servicios {
  display: grid;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  color: #000000;
  text-align: center;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  margin-bottom: 1rem;
  cursor: pointer;
}
.listado-servicios .servicio.servicio-seleccionado {
  background-color: rgba(8, 146, 226, 0.8431372549);
  color: #FFFFFF;
}
.listado-servicios .servicio.servicio-seleccionado .precio-servicio {
  color: #FFFFFF;
}
.listado-servicios .servicio:last-child {
  margin-bottom: 0;
}
.listado-servicios .servicio:hover {
  transform: scale(1.05);
}
.listado-servicios .servicio .precio-servicio {
  color: rgba(8, 146, 226, 0.8431372549);
  font-weight: 700;
  font-size: 2.5rem;
  margin: 0;
}
.listado-servicios .servicio .nombre-servicio {
  font-weight: 700;
  margin-bottom: 1rem;
}

.seccion-resumen h3 {
  font-weight: 700;
}
.seccion-resumen p {
  font-weight: 700;
}
.seccion-resumen span {
  color: rgba(8, 146, 226, 0.8431372549);
  font-weight: 700;
}

.contenedor-servicio {
  margin-bottom: 1rem;
  border-bottom: 1px solid #e1e1e1;
}

.cita, .servicio {
  list-style: none;
  border-top: 1px solid #e1e1e1;
}
.cita p, .servicio p {
  font-weight: 700;
  color: rgba(8, 146, 226, 0.8431372549);
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
}
.cita span, .servicio span, .cita .servicio, .servicio .servicio {
  color: #FFFFFF;
  font-weight: 400;
}
.cita .servicio span, .servicio .servicio span {
  color: rgba(8, 146, 226, 0.8431372549);
}

.servicios form {
  text-align: center;
  margin-bottom: 0.5rem;
}

.boton-rojo {
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .boton-rojo {
    display: inline-block;
    width: auto;
  }
}

.boton.actualizar {
  line-height: normal;
}

.servicio .acciones {
  margin-top: 2rem;
}

.form {
  width: 95%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.form button, .form input, .form optgroup, .form select, .form textarea {
  font-family: "Roboto", sans-serif;
}
.form .campo {
  width: 100%;
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.form .campo label {
  text-align: center;
  flex: 0 0 10rem;
}
.form .campo input {
  flex: 1;
  border: none;
  padding: 1rem;
  border-radius: 1rem;
}
.form .campo input:disabled {
  background-color: #999999;
  color: #FFFFFF;
}

.boton {
  font-family: "Roboto", sans-serif;
  background-color: rgba(8, 146, 226, 0.8431372549);
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton:hover {
  background-color: rgba(7, 130, 201, 0.8431372549);
  cursor: pointer;
}

.acciones {
  margin-top: 3.3333333333rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-around;
  }
}
.acciones a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 1.25rem;
  text-align: center;
}

.alerta {
  padding: 0.7142857143rem;
  color: #FFFFFF;
  width: 95%;
  display: block;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  margin-left: auto;
  margin-right: auto;
}
.alerta.error {
  background-color: rgba(224, 19, 19, 0.8470588235);
}
.alerta.exito {
  background-color: #71B100;
}

.tabs {
  width: 95%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: rgba(8, 146, 226, 0.8431372549);
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  font-weight: 700;
  text-transform: uppercase;
  transition: background-color 300ms ease;
}
.tabs button:hover {
  cursor: pointer;
  background-color: rgba(6, 114, 177, 0.8431372549);
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: rgba(8, 146, 226, 0.8431372549);
}

.paginacion {
  width: 95%;
  margin: 0 auto;
  padding: 5rem 0;
}
.paginacion button {
  margin: 0 0 1rem 0;
  width: 100%;
}
@media (min-width: 480px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
  .paginacion button {
    width: min-content;
  }
}

.ocultar {
  visibility: hidden;
}